/* global Parse, FB */
import React, { useEffect } from "react";
import {
  Stack,
  Grommet,
  Box,
  Button,
  Menu,
  Anchor,
  Text,
  Footer,
} from "grommet";
import "../pages/index.css";
import "./i18n";
import { Adsense } from "@ctrl/react-adsense";
import { assign } from "../helpers";
import { Tools, Cart } from "grommet-icons";
import { navigateTo } from "gatsby-link";

const Home = ({ children }) => {
  const accountLabel = Parse.User.current()
    ? Parse.User.current().get("email")
    : "account";
  const logoutMenu = [
    {
      label: "Sign Out",
      onClick: () => {
        Parse.User.logOut();
        navigateTo("/");
      },
    },
  ];
  const accountMenu =
    accountLabel === "account"
      ? [
          {
            label: "Sign in",
            onClick: () => {
              Parse.User.logOut();
              navigateTo("/account");
            },
          },
        ]
      : logoutMenu;
  return (
    <Box>
      <Box
        gridArea="header"
        direction="row"
        align="center"
        pad={{ horizontal: "medium", vertical: "small" }}
        background="brand"
      >
        <Box>
          <Button
            icon={<Tools />}
            label="BidBid"
            margin={{ right: "20px" }}
            onClick={() => {
              assign("/");
            }}
          />
        </Box>
        <Box margin={{ left: "auto" }}>
          <Menu label={accountLabel} items={accountMenu} />
        </Box>
      </Box>

      <Box align="center" justify="center" pad="small">
        <Box
          pad={{
            bottom: "64px",
          }}
          fill="horizontal"
          width={{
            min: "360px",
          }}
        >
          {children}
        </Box>
      </Box>
      <Footer margin={{ top: "auto" }} background="brand" pad="medium">
        <Text>Copyright</Text>
      </Footer>
    </Box>
  );
};

export default Home;
